.img-box {
    width: 100px;
    border-radius: 50%;
    overflow: hidden;
    text-align: center;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.18);
    img {
        width: 100%;
        height: auto;
    }
}

@media (max-width: 765px) {
    .img-box {
        width: 70px;
    }
}

@media (max-width: 765px) {
    .menu {
        display: none;
        transition: 0.3s;
    }
    .menu_active {
        display: block;
        transition: 0.3s;
    }
}

.js-hamburger {
    display: none;
    @media (max-width: 765px) {
        display: block;
        background-color: $color2;
        position: absolute;
        right: 30px;
        top: 10px;
        padding: 10px;
        cursor: pointer;
    }
}

.item {
	padding-top: 35px;
    a {
        text-decoration: none;
        font-weight: 600;
        font-size: 20px;
        font-family: 'Geometria';
        color: $color1;
        &:hover {
            color: $color2;
            padding-bottom: 5px;
            border-bottom: 1px solid $color2;
            transition: 0.3s;
        }
    }
}

@media (max-width: 765px) {
    .item {
        padding-top: 15px;
        margin-bottom: 15px;
        margin-left: 50px;
    }
}


.section-1 {
    padding-top: 30px;
    position: relative;
    z-index: 30;
    background-color: #fcfeff;
    overflow: hidden;
    position: fixed;
    padding-top: 10px;
    padding-bottom: 10px;
    /* Set the navbar to fixed position */
    top: 0;
    right: 0;
    /* Position the navbar at the top of the page */
    width: 100%;
    /* Full width */
    .col-lg-8 {}
}


.section-2 {
    margin-top: 80px;
    background: url(../img/img-1.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 667px;
    box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.18);
    position: relative;
    z-index: 1;
    .txt {
        position: absolute;
        bottom: 15%;
        right: 5%;
    }
    /** fadeInLeft **/
    @-webkit-keyframes fadeInLeft {
        from {
            opacity: 0;
            -webkit-transform: translatex(-10px);
            -moz-transform: translatex(-10px);
            -o-transform: translatex(-10px);
            transform: translatex(-10px);
        }
        to {
            opacity: 1;
            -webkit-transform: translatex(0);
            -moz-transform: translatex(0);
            -o-transform: translatex(0);
            transform: translatex(0);
        }
    }

    @-moz-keyframes fadeInLeft {
        from {
            opacity: 0;
            -webkit-transform: translatex(-10px);
            -moz-transform: translatex(-10px);
            -o-transform: translatex(-10px);
            transform: translatex(-10px);
        }
        to {
            opacity: 1;
            -webkit-transform: translatex(0);
            -moz-transform: translatex(0);
            -o-transform: translatex(0);
            transform: translatex(0);
        }
    }

    @keyframes fadeInLeft {
        from {
            opacity: 0;
            -webkit-transform: translatex(-100px);
            -moz-transform: translatex(-100px);
            -o-transform: translatex(-100px);
            transform: translatex(-100px);
        }
        to {
            opacity: 1;
            -webkit-transform: translatex(0);
            -moz-transform: translatex(0);
            -o-transform: translatex(0);
            transform: translatex(0);
        }
    }

    .in-left {
        -webkit-animation-name: fadeInLeft;
        -moz-animation-name: fadeInLeft;
        -o-animation-name: fadeInLeft;
        animation-name: fadeInLeft;
        -webkit-animation-fill-mode: both;
        -moz-animation-fill-mode: both;
        -o-animation-fill-mode: both;
        animation-fill-mode: both;
        -webkit-animation-duration: 5s;
        -moz-animation-duration: 5s;
        -o-animation-duration: 5s;
        animation-duration: 5s;
        -webkit-animation-delay: 1s;
        -moz-animation-delay: 1s;
        -o-animation-duration: 1s;
        animation-delay: 1s;
        font-size: 50px;
        color: #fff;
        font-weight: 600;
        font-family: 'Dancing Script', cursive;
        font-size: 70px;
    }
}

@media (max-width: 765px) {
    .section-2 {
        height: 330px;
        .in-left {
            font-size: 30px;
        }
    }
}

.section-3 {
    .head {
        text-align: center;
        color: $color1;
        font-weight: 600;
        font-family: 'Geometria';
        margin-top: 100px;
        padding-bottom: 45px;
        font-size: 60px;
    }

    .paragraph {
        text-align: justify;
        color: $color1;
        font-weight: 300;
        font-family: 'Geometria';
        margin-bottom: 100px;
        padding-bottom: 50px;
        font-size: 40px;
        line-height: 48px;
        border-bottom: 1px solid $color2;
    }
}

@media (max-width: 765px) {
    .section-3 {
        .head {
            font-size: 40px;
            margin-top: 50px;
            padding-bottom: 20px;
        }
        .paragraph {
            font-size: 20px;
            line-height: 30px;
            padding-bottom: 30px;
            margin-bottom: 50px;
        }
    }
}

.section-4 {
    background: url(../img/img-2.jpg);
    width: 100%;
    height: 300px;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

@media (max-width: 765px) {
    .section-4 {
        height: 200px;
    }
}


.section-5 {
    text-align: center;
    margin-top: 70px;
    .h1 {
        color: $color1;
        font-weight: 600;
        font-family: 'Dancing Script', cursive;
        font-size: 70px;
    }

    .h2 {
        color: $color2;
        font-weight: 500;
        font-family: 'Geometria';
        font-size: 40px;
        padding-top: 5px;
    }

    .h3 {
        color: $color2;
        font-weight: 400;
        font-family: 'Geometria';
        font-size: 30px;
        padding-bottom: 50px;
        padding-top: 15px;
    }

    .slick {
        width: 100%;
        margin-bottom: 100px;
    }

    .slide {
        padding: 0px 20px;
        img {
            width: 100%;
            height: auto;
        }
    }
}

@media (max-width: 765px) {
    .section-5 {
        margin-top: 40px;
        .h1 {
            font-size: 30px;
        }
        .h2 {
            font-size: 20px;
        }
        .h3 {
            font-size: 20px;
            padding-bottom: 30px;
        }
    }
}


.section-6 {
    /* Set a specific height */
    height: 500px;
    background: url(../img/img-4.jpg);
    width: 100%; // background-attachment: scroll;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.footer {
    text-align: center;
    color: $color1;
    font-weight: 500;
    font-family: 'Geometria';
    font-size: 30px;
    padding-bottom: 50px;
    padding-top: 50px;
}

@media (max-width: 765px) {
    .section-6 {
        height: 200px;
    }
}

@media (max-width: 765px) {
    .footer {
        font-size: 20px;
    }
}