.up-button {
	background: url(../img/up-btn.svg) center top no-repeat;
	width: 80px;
	height: 80px;
	cursor: pointer;
	background-color: #fff;
	border-radius: 50%;
	background-size: 100% 100%;
	position: fixed;
	z-index: 10;
	bottom: 80px;
	right: 100px;
	transform: translateY(160px);
	transition: .3s ease;
	@media (max-width: 765px) {

		width: 55px;
		height: 55px;
		right: 25px;
		bottom: 25px;
	}


	&:hover {
		background-color:$color2;
	}

	&_show {
		transform: translateY(0);
	}
}